'use client';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import Link from 'next/link';
import { ArrowRight, Church } from 'lucide-react';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';

export default function LoginPage() {
  const { login, isAdmin } = useAuth();
  const router = useRouter();

  useEffect(() => {
    // Auto-login as admin for testing
    login(true);
  }, [login]);

  useEffect(() => {
    if (isAdmin) {
      router.push('/admin');
    } else {
      router.push('/my-schedule');
    }
  }, [isAdmin, router]);

  const handleLogin = (asAdmin: boolean) => {
    login(asAdmin);
    router.push(asAdmin ? '/admin' : '/my-schedule');
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-8 bg-gradient-to-br from-background to-blue-200">
      <div className="text-center mb-12">
        <Church className="h-16 w-16 text-primary mx-auto mb-4" />
        <h1 className="text-5xl font-bold font-headline text-foreground tracking-tight sm:text-6xl">
          Welcome to Weaver's Loom
        </h1>
        <p className="mt-4 text-lg text-foreground/80 max-w-2xl mx-auto">
          The volunteer scheduling module for ParishScribe.
        </p>
      </div>

      <Card className="w-full max-w-md bg-card/80 backdrop-blur-sm border-border/20 shadow-lg">
        <CardHeader>
          <CardTitle>Login</CardTitle>
          <CardDescription>Select your role to continue.</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
           <p className="text-sm text-center text-muted-foreground">
            For testing, you are being automatically logged in.
          </p>
        </CardContent>
      </Card>
       <footer className="absolute bottom-4 text-center text-foreground/60 text-sm">
        <p>A module of ParishScribe</p>
      </footer>
    </main>
  );
}
